.progress-info.progress-striped .bar, .progress-striped .bar-info {
    background-color: #2824b6;
    background-image: linear-gradient(45deg, hsla(0, 0%, 100%, .15) 25%, transparent 0, transparent 50%, hsla(0, 0%, 100%, .15) 0, hsla(0, 0%, 100%, .15) 75%, transparent 0, transparent);
}

.progress.active .bar {
    animation: progress-bar-stripes 2s linear infinite;
}

.progress-info .bar, .progress .bar-info {
    background-color: #4bb1cf;
    background-image: linear-gradient(180deg, #5bc0de, #339bb9);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ff5bc0de", endColorstr="#ff339bb9", GradientType=0);
}

.progress-striped .bar {
    background-color: #149bdf;
    background-image: linear-gradient(45deg, hsla(0, 0%, 100%, .15) 25%, transparent 0, transparent 50%, hsla(0, 0%, 100%, .15) 0, hsla(0, 0%, 100%, .15) 75%, transparent 0, transparent);
    background-size: 40px 40px;
}

.progress .bar {
    float: left;
    width: 0;
    height: 100%;
    font-size: 12px;
    color: #fff;
    text-align: center;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, .25);
    background-color: #0e90d2;
    background-image: linear-gradient(180deg, #149bdf, #0480be);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ff149bdf", endColorstr="#ff0480be", GradientType=0);
    box-shadow: inset 0 -1px 0 rgba(0, 0, 0, .15);
    box-sizing: border-box;
    transition: width .6s ease;
}

.progress, .progress .bar {
    background-repeat: repeat-x;
}

.progress {
    height: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: #f7f7f7;
    background-image: linear-gradient(180deg, #f5f5f5, #f9f9f9);
    border-radius: 4px;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#fff5f5f5", endColorstr="#fff9f9f9", GradientType=0);
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
}

.progress .bar.width-15 {
    width: 15%;
}

.progress .bar.width-50 {
    width: 50%;
}

.progress .bar.width-100 {
    width: 100%;
}



