.kraken-header .header-logo {
    width: 200px;
    height: 100px;
    background-image: url(/static/media/kraken-horizontal.9c997b8d.svg);
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
    display: block;
    color: transparent;
    overflow: hidden;
    position: relative;
    text-indent: -99999em;
    flex-grow: 0;
    margin: 0 78px 0 0;
    z-index: 99;
    box-sizing: content-box;

}
.kraken-header .top-nav {
    flex-grow: 1;
    display: flex;
}

.kraken-header .top-nav>ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    z-index: 6;
    position: relative;
    flex-grow: 1;
}
.kraken-header li, .kraken-header ol, .kraken-header ul {
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}


.kraken-header .top-nav>ul.primary>li {
    margin: 0 32px 0 0;
}

.kraken-header .top-nav>ul.primary>li {
    flex-grow: 0;
    position: relative;
    flex-shrink: 1;
}
.kraken-header li, .kraken-header ol, .kraken-header ul {
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}
.kraken-header .top-nav>ul.primary>li>a, .kraken-header .top-nav>ul.primary>li>span {
    padding-top: 0;
    font-weight: 700;
    font-size: 13px;
}
.kraken-header .top-nav>ul.primary>li>a {
    opacity: 1;
    padding-top: 0;
}



