    visibility: hidden;
}
.kraken-header .top-nav>ul.primary>li>ul {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(0);
    padding: 40px 0;
    overflow: hidden;
    width: 50vw;
    font-size: 16px;
    line-height: 30px;
}
.kraken-header .top-nav>ul.primary>li>ul li {
    margin: 0;
    display: block;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}
.kraken-header li, .kraken-header ol, .kraken-header ul {
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}
.kraken-header .top-nav>ul.primary>li>ul li>div {
    margin: 0 40px 0 0;
    width: auto;
}
.kraken-header .top-nav>ul.primary>li>ul ul, .kraken-header .top-nav>ul.primary>li>ul ul ul {
    opacity: 1;
    transform: none;
    height: auto;
    position: relative;
    flex-direction: column;
}
.kraken-header .top-nav>ul.primary>li>ul li>div>ul li {
    opacity: .6;
}

.kraken-header .top-nav>ul.primary>li>ul li {
    margin: 0;
    display: block;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}
.kraken-header li, .kraken-header ol, .kraken-header ul {
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

